import styled from 'styled-components'
import {NavLink} from 'react-router-dom'
import {Field} from 'react-final-form'
import {DefaultButtonStyles, TextAreaStyle, TextInputStyle} from '../../app-styled-components'

export let Container = styled.div`
    height: 100%;
    overflow-y: overlay;
    position: relative;
`

export let BackButton = styled(NavLink)`
    position: absolute;
    left: 33px;
    top: 64px;
`

export let FormBox = styled.form`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
`

export let InputText = styled(Field)`
${TextInputStyle}
margin-top: 20px;
`

export let InputTextArea = styled(Field)`
${TextAreaStyle}
    height: 152px;
    margin-top: 20px;
`

export let CategoryTitle = styled.p`
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    text-transform: uppercase;
    color: ${props => props.isError ? '#AB081B' : 'rgba(255,255,255,0.7)'};
    text-align: center;
    margin-top: 30px;
    margin-bottom: 10px;
    transition: 0.25s;
`

export let CategoriesLine = styled.div`
    height:  ${props => props.active ? '0' : '59px'};
    opacity:  ${props => props.active ? '0' : '1'};
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    transition: 0.25s;
    width: 100%;
    
    div:first-child {
    margin-left: 42px;
    }
    
   ::-webkit-scrollbar {
    height: 0px;
    }

`

export let MarginBlock = styled.div`
width: 34px;
height: 8px;
opacity: 0;
p {
width: 34px;
}
`

export let DefaultButton = styled.div`
height: 43px;
background: radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%);
box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
border-radius: 47px;
font-style: normal;
font-weight: 800;
font-size: 14px;
line-height: 52px;
letter-spacing: -0.32px;
text-transform: uppercase;
color: white;
cursor: pointer;
width: min-content;
padding: 0 14px;
display: flex;
align-items: center;
margin: 8px 14px 8px 0;
user-select:none;
transition: 0.25s;
 -webkit-tap-highlight-color: rgba(0,0,0,0); 
 -webkit-tap-highlight-color: transparent;
`
export let Button = styled(DefaultButton)`
color: ${props => props.active ? 'rgba(0, 0, 0, 0.8)' : 'white'};
background: ${props => props.active ? 'white' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`

export let YellowButton = styled(DefaultButton)`
background: ${props => props.active ? '#BFB439' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`

export let GreenButton = styled(DefaultButton)`
background: ${props => props.active ? '#36B782' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`

export let PurpleButton = styled(DefaultButton)`
background: ${props => props.active ? '#8B3BBE' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`

export let RedButton = styled(DefaultButton)`
background: ${props => props.active ? '#BF3339' : 'radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 99.99%)'};
`

export let SubmitButton = styled.button`
${DefaultButtonStyles}
margin-top: 40px;
margin-bottom: 60px;
`