import {AxiosResponse} from 'axios'
import {ThunkAction} from 'redux-thunk'
import {AppStateType} from './store'
import MainApi from '../api/main-api'

///////////// Const for action creators
const CHANGE_FORM_STATE = 'CHANGE_FORM_STATE'
const AD_ADDED = 'AD_ADDED'
const CLOSE_ADD_AD_PAGE = 'CLOSE_ADD_AD_PAGE'

type InitialStateMainPageDataType = {
    formState: null | FormStateType
    isAdAdded: boolean
}

///////////// Initial state
let initialState: InitialStateMainPageDataType = {
    formState: {
        photo: null,
        name: null,
        description: null,
        category: [false, false, false, false, false],
        categoryId: null,
        subCategory: [false, false, false, false, false],
        subCategoryId: null
    },
    isAdAdded: false
}

///////////// Reducer
const AddAdReducer = (state = initialState, action: ActionsTypes): InitialStateMainPageDataType => {
    switch (action.type) {
        case CHANGE_FORM_STATE: {
            return {
                ...state,
                formState: action.state
            }
        }
        case AD_ADDED: {
            return {
                ...state,
                isAdAdded: true
            }
        }
        case CLOSE_ADD_AD_PAGE: {
            return {
                formState: {
                    photo: null,
                    name: null,
                    description: null,
                    category: [false, false, false, false, false],
                    categoryId: null,
                    subCategory: [false, false, false, false, false],
                    subCategoryId: null
                },
                isAdAdded: false
            }
        }
        default:
            {
                return state
            }
        }

    }

///////////// Action creators
    type ActionsTypes =
        ChangeFormStateType |
        AdAddedType |
        CloseAddAdPageType
    
    type CloseAddAdPageType = {
        type: typeof CLOSE_ADD_AD_PAGE
    }

    export let CloseAddAdPage = () => ({type: CLOSE_ADD_AD_PAGE})

    type AdAddedType = {
        type: typeof AD_ADDED
    }

    export let AdAdded = (): AdAddedType => ({type: AD_ADDED})

    type ChangeFormStateType = {
        type: typeof CHANGE_FORM_STATE
        state: any
    }

    export let ChangeAddAdFormState = (state: any): ChangeFormStateType => ({type: CHANGE_FORM_STATE, state})

///// Thanks
    type ThunkActions = ThunkAction<Promise<void>, AppStateType, unknown, ActionsTypes>

    export let AddAdThunk = (formData: FormStateType): ThunkActions => async (dispatch) => {

        let payload = new FormData()

        if (typeof formData.name === "string") {
            payload.append('name', formData.name)
        }
        if (typeof formData.description === "string") {
            payload.append('description', formData.description)
        }
        // if (formData.photo !== null && formData.photo.length !== 0) {
        //     payload.append('photo', formData.photo[0])
        // }
        if (formData.categoryId !== null) {
            payload.append('categoryId', '' + formData.categoryId)
        }
        if (formData.subCategoryId !== null) {
            payload.append('subCategoryId', '' + formData.subCategoryId)
        }

        const response: AxiosResponse<any> = await MainApi.CreateAd(payload)

        if (response.status === 201) {
            dispatch(AdAdded())
        }
    }


    export default AddAdReducer


////// types for state

    export type FormStateType = {
        photo: any | null
        name: string | null
        description: string | null
        category: boolean[]
        categoryId: null | number
        subCategory: boolean[]
        subCategoryId: null | number
    }