import React, {useEffect, useRef, useState} from 'react'
import styled from 'styled-components'


// @ts-ignore
const TextareaField = ({input, meta, ...props}) => {

    let hasError = meta.touched && meta.error

    const [cursor, setCursor] = useState(1)
    const inputRef = useRef(null)

    useEffect(() => {
        inputRef.current.selectionStart = cursor
        inputRef.current.selectionEnd = cursor
    }, [input.value])

    let newOnChange = (e) => {
        setCursor(e.target.selectionStart)
        props.onChange(e)
    }

    let newProps = {
        ...props,
        onChange: newOnChange
    }

    return <FieldContainer>
        <textarea
            {...input}
            {...newProps}
            ref={inputRef}
        />
        {hasError && <ErrorSpan>{meta.error}</ErrorSpan>}
    </FieldContainer>
}

export default TextareaField


let FieldContainer = styled.div`
    width: inherit;
    height: inherit;
    position: relative;
   display: flex;
    justify-content: center;
`

let ErrorSpan = styled.span`
position: absolute;
    color: red;
    bottom: -20px;
`