import React, {FC, useState} from 'react'
import {connect} from 'react-redux'
import {AppStateType} from '../../../store/store'
import {AdDataType} from '../../../types/types'
import {
    Card,
    Close,
    Container,
    Headline,
    Info,
    Photo,
    Label,
    Description,
    Date,
    Buttons,
    Phone,
    Message,
    Like, Author
} from './style'
import {PasteSmallPlug} from '../../../services/paste_small_plug'
import likeIcon from '../../../images/like.svg'
import closeIcon from '../../../images/white-close.svg'
import phoneIcon from '../../../images/phone.svg'
import authorIcon from '../../../images/photo.png'
import messageIcon from '../../../images/message.svg'

///////////// types for props
type PropsType = {
    local: string[],
    ad: any,
    SetSelectedAd: (data: AdDataType | null) => void
}

///// for mock
function randomInteger(min: number, max: number) {
    let rand = min - 0.5 + Math.random() * (max - min + 1)
    return Math.round(rand)
}
let colors = [undefined, '#BFB439', '#35B881', '#8B3BBE', '#BF3339']
/////

let BigAdCard: FC<PropsType> = ({
                                      local,
                                      ad,
                                      SetSelectedAd
    }) => {

    let {
        adId,
        headline,
        isLike,
        image,
        categoryId,
        userName,
        date,
        description,
        time
    } = ad
    
    ///// for save plug image
    const [imageCard] = useState(PasteSmallPlug())
    const [category] = useState(randomInteger(1, 4))
    
  
    let imageAd = imageCard
    if (image !== null) {
        imageAd = image
    }

    return <Container>
        <Card>
            <Close onClick={()=>{SetSelectedAd(null)}} src={closeIcon} alt={'закрити'} />
            <Photo src={image} alt={'картинка оголошення'} />
            <Info>
                <Headline>{headline}</Headline>
                <Label color={colors[category]}>{local[category]}</Label>
                <Description>{description}</Description>
                <Date>{date} {time}</Date>
                <Buttons>
                    <Phone to='/'>
                        <img src={phoneIcon} alt='зателефонувати'/>
                    </Phone>
                    <Message to='/'>
                        <img src={messageIcon} alt='написати'/>
                    </Message>
                </Buttons>
                <Like src={likeIcon} alt='лайк' />
                <Author>
                    <img src={authorIcon} alt='автор'/>
                    <p>{userName}</p>
                </Author>
            </Info>
        </Card>
    </Container> 
}

///////////// create props for component
let mapStateToProps = (state: AppStateType) => {
    return {
        local: state.LocalizationReducer.langData.big_ad_card
    }
}


export default connect(mapStateToProps, {})(BigAdCard)