import React, {FC, useState} from 'react'
import {Form} from 'react-final-form'
import {
    BackButton,
    CategoryTitle,
    Container,
    FormBox,
    InputTextArea,
    InputText,
    CategoriesLine,
    Button,
    GreenButton,
    MarginBlock,
    PurpleButton,
    RedButton,
    YellowButton, SubmitButton
} from './style'
import backIcon from '../../images/back-icon.svg'
import UploadAdPhoto from '../common-components/fields/upload-ad-photo'
import {composeValidators, required} from '../../services/validations'
import InputFinalFormField from '../common-components/fields/input-final-form-field'
import {ChangeState} from '../../services/change_state'
import {FormStateType} from '../../store/add-ad-reducer'
import TextareaField from '../common-components/fields/textarea-field'

///////////// types for props
type PropsType = {
    ChangeAddAdFormState: (state: FormStateType) => void
    AddAdThunk: (formData: FormStateType) => void
    
    local: string[]
    formState: any
}



let AddAd: FC<PropsType> = ({local, ChangeAddAdFormState, formState, AddAdThunk}) => {
    
    const [showLoad, setLoad] = useState(false)
    const [categoryValidation, setCategoryValidation] = useState(false)
    
    let ChangePhoto = (name: string, value: any) => {
        ChangeAddAdFormState(ChangeState(name, value, formState))
    }

    let ChangeCategory = (value: number) => {
        let newCategoryArray = []
        newCategoryArray[value] = true
        let newState = {
            ...formState,
            category: newCategoryArray,
            categoryId: value
        }
        ChangeAddAdFormState(newState)
        setCategoryValidation(false)
    }

    let ChangeSubCategory = (value: number) => {
        let newCategoryArray = []
        newCategoryArray[value] = true
        let newState = {
            ...formState,
            subCategory: newCategoryArray,
            subCategoryId: value
        }
        if (formState.subCategory[value]) {
            newState = {
                ...formState,
                subCategory: [false,false,false,false,false],
                subCategoryId: null
            }
        }
        ChangeAddAdFormState(newState)
    }
    
    let onSubmit = (formData: any) => {
        if (formState.name !== null && formState.categoryId !== null) {
            setLoad(true)
            AddAdThunk(formData)
        } else if (formState.categoryId === null) {
            setCategoryValidation(true)
        }
    }
    
    return <Container>
        <BackButton to={'/'}>
            <img src={backIcon} alt={local[0]}/>
        </BackButton>
        <Form onSubmit={onSubmit}
              initialValues={formState}
              render={({handleSubmit}) => (
                  <FormBox onSubmit={handleSubmit}>
                      <UploadAdPhoto
                          ChangeState={ChangePhoto}
                      />
                      <InputText
                          validate={composeValidators(required)}
                          name='name'
                          onChange={(e: any)=>{
                              ChangeAddAdFormState(ChangeState('name', e.target.value, formState))
                          }}
                          component={InputFinalFormField}
                          placeholder={local[3]}
                      />
                      <InputTextArea
                          name='description'
                          onChange={(e: any)=>{
                              ChangeAddAdFormState(ChangeState('description', e.target.value, formState))
                          }}
                          component={TextareaField}
                          placeholder={local[4]}
                      />
                          {/*@ts-ignore*/}
                      <CategoryTitle isError={categoryValidation}>{local[15]}</CategoryTitle>
                      <CategoriesLine>
                          {/*@ts-ignore*/}
                          <YellowButton active={formState.category[1]} onClick={()=>{ChangeCategory(1)}}>{local[5]}</YellowButton>
                          {/*@ts-ignore*/}
                          <GreenButton active={formState.category[2]} onClick={()=>{ChangeCategory(2)}}>{local[6]}</GreenButton>
                          {/*@ts-ignore*/}
                          <PurpleButton active={formState.category[3]} onClick={()=>{ChangeCategory(3)}}>{local[7]}</PurpleButton>
                          {/*@ts-ignore*/}
                          <RedButton active={formState.category[4]} onClick={()=>{ChangeCategory(4)}}>{local[8]}</RedButton>
                          <MarginBlock><p>1</p></MarginBlock>
                      </CategoriesLine>
                      <CategoryTitle>{local[16]}</CategoryTitle>
                      <CategoriesLine>
                          {/*@ts-ignore*/}
                          <Button active={formState.subCategory[1]} onClick={()=>{ChangeSubCategory(1)}}>{local[9]}</Button>
                          {/*@ts-ignore*/}
                          <Button active={formState.subCategory[2]} onClick={()=>{ChangeSubCategory(2)}}>{local[10]}</Button>
                          {/*@ts-ignore*/}
                          <Button active={formState.subCategory[3]} onClick={()=>{ChangeSubCategory(3)}}>{local[11]}</Button>
                          {/*@ts-ignore*/}
                          <Button active={formState.subCategory[4]} onClick={()=>{ChangeSubCategory(4)}}>{local[12]}</Button>
                          <MarginBlock><p>1</p></MarginBlock>
                      </CategoriesLine>
                      {showLoad
                      ?<SubmitButton type={'button'}>{local[14]}</SubmitButton>
                      :<SubmitButton type={'submit'}>{local[13]}</SubmitButton>}
                      
                  </FormBox>
              )}
        />
    </Container>
}


export default AddAd