import React, {useEffect, useRef, useState} from 'react'
import styled from 'styled-components'


// @ts-ignore
const InputFinalFormField = ({input, meta, ...props}) => {

    let hasError = meta.touched && meta.error
    let error = false
    if (hasError) {
        error = true
    }

    const [cursor, setCursor] = useState(1)
    const inputRef = useRef(null)

    useEffect(() => {
        inputRef.current.selectionStart = cursor
        inputRef.current.selectionEnd = cursor
    }, [input.value])

    let newOnChange = (e) => {
        setCursor(e.target.selectionStart)
        props.onChange(e)
    }

    let newProps = {
        ...props,
        onChange: newOnChange
    }

    return <FieldContainer isError={error} >
        <input
            {...input}
            {...newProps}
            ref={inputRef}
        />
    </FieldContainer>
}

export default InputFinalFormField


let FieldContainer = styled.div`
    width: inherit;
    height: inherit;
    position: relative;
   display: flex;
    justify-content: center;
    
    input {
    border: 3px solid ${props => props.isError ? '#AB081B' : 'rgba(0,0,0,0.8)'} !important;
    }
`