import styled from 'styled-components'
import {Link, NavLink} from 'react-router-dom'

export let Card = styled.div`
display: flex;
position: relative;
height: 559px;
width: calc(100% - 36px);
background: rgba(0, 0, 0, 0.8);
box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
border-radius: 47px;
flex-direction: column;
align-items: center;
min-width: 285px;
`

export let Container = styled.div`
position: fixed;
height: 100%;
width: 100%;
backdrop-filter: blur(20px);
top: 0;
z-index: 10;
display: flex;
align-items: center;
justify-content: center;
`

export let Close = styled.img`
position: absolute;
right: 0px;
top: -54px;
`

export let Photo = styled.img`
height: 223px;
width: 100%;
object-fit: cover;
border-radius: 47px 47px 0px 0px;
`
export let Info = styled.div`
width: calc(100% - 60px);
position: relative;
`

export let Headline = styled.p`
font-style: normal;
font-weight: 800;
font-size: 14px;
line-height: 18px;
text-transform: uppercase;
color: #FFFFFF;
margin-top: 24px;
`

export let Label = styled.div`
margin-top: 21px;
background: ${props => props.color};
box-shadow: 0px 0px 40px 1px rgba(0, 0, 0, 0.21);
border-radius: 49px;
width: 132px;
height: 26px;
display: flex;
align-items: center;
justify-content: center;
color: white;
font-style: normal;
font-weight: 800;
font-size: 13px;
letter-spacing: -0.32px;
text-transform: uppercase;
`

export let Description = styled.p`
font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 13px;
color: rgba(255,255,255,0.7);
margin-top: 21px;
height: 75px;
margin-right: 46px;
`

export let Date = styled.p`
margin-top: 55px;
font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 13px;
color: rgba(255, 255, 255, 0.7);
`

export let Buttons = styled.div`
width: 100%;
margin-top: 23px;
display: flex;
align-items: center;
justify-content: center;
position: relative;
`

 let button = `

`

export let Phone = styled(Link)`
${button}
position: absolute;
left: 0;
top: 0;
`

export let Message = styled(NavLink)`
${button}
`

export let Like = styled.img`
position: absolute;
right: 0;
top: 18px;
`

export let Author = styled.div`
position: absolute;
right: 0;
top: 188px;
display: flex;
flex-direction: column;
align-items: center;

p {
font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 13px;
color: rgba(255, 255, 255, 0.7);
margin-top: 7px;
}
`
