import React, {FC, useEffect} from 'react'
import {AppStateType} from '../../store/store'
import { Redirect } from 'react-router-dom'
import {connect, ConnectedProps} from 'react-redux'
import AddAd from './add-ad'
import {AddAdThunk, ChangeAddAdFormState, CloseAddAdPage} from '../../store/add-ad-reducer'


type PropsType = ConnectedProps<typeof connector>

let AddAdContainer: FC<PropsType> = ({
                                         local, 
                                         ChangeAddAdFormState, 
                                         formState, 
                                         AddAdThunk,
                                         isAdAdded,
                                         CloseAddAdPage
}) => {
    
    useEffect(()=>{
        return () => {
            CloseAddAdPage()
        }
    },[])
    
    let PropsAddAd = {
        local,
        ChangeAddAdFormState,
        formState,
        AddAdThunk
    }
    
    if (isAdAdded) {
        return <Redirect to={'/'} />
    } else {
        return <AddAd {...PropsAddAd}/>
    }
}

///////////// create props for component
let mapStateToProps = (state: AppStateType) => {
    return {
        local: state.LocalizationReducer.langData.add_ad_page,
        formState: state.AddAdReducer.formState,
        isAdAdded: state.AddAdReducer.isAdAdded
    }
}

const connector = connect(mapStateToProps, {
    ChangeAddAdFormState,
    AddAdThunk,
    CloseAddAdPage
})
export default connector(AddAdContainer)
