import React, {FC, useState} from 'react'
import ChooseCategoryContainer from '../common-components/choose-category/choose-category-container'
import {Ads, BottomSeparator, Container} from './style'
import {IsScroll} from '../../services/scroll_listener'
import Navigation from '../common-components/navigation/navigation'
import SmallAdCard from '../common-components/small-ad-card/small-ad-card'
import Preloader from '../common-components/preloader/preloader'
import BigAdCard from '../common-components/big-ad-card/big-ad-card'
import {AdDataType} from '../../types/types'


///////////// types for props
type PropsType = {
    ads: any[] | null
    selectedAd: AdDataType | null
    SetSelectedAd: (data: AdDataType | null) => void
}

let Main: FC<PropsType> = ({ads, selectedAd, SetSelectedAd}) => {
    
    ///// state for listen scroll up or down
    const [scrollPosition, setScrollPosition] = useState([0,0])
    const [upOrDownScroll, setScrollState] = useState([false, false])
    
    let adsArray
    let snowPreloader = true
    
    if (ads !== null) {
         adsArray = ads.map((i: any) => {
            return <SmallAdCard ad={i} SetSelectedAd={SetSelectedAd} />
        })
        snowPreloader = false
    } 

    return <Container onScroll={(e: any)=>{IsScroll(e, upOrDownScroll, setScrollState, scrollPosition, setScrollPosition)}}>
    <ChooseCategoryContainer upOrDownScroll={upOrDownScroll} />
    <Ads>
        {snowPreloader
        ? <Preloader full={false} height={80} />
            : <>{adsArray} <BottomSeparator><p></p></BottomSeparator></>}
    </Ads>
        <Navigation />
        {selectedAd !== null && <BigAdCard ad={selectedAd} SetSelectedAd={SetSelectedAd}/>}
    </Container>
}


export default Main